
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'LingPickerTime',
components: {
Group,
XSwitch,
XDialog,
Flexbox,
FlexboxItem,
XHeader,
XButton,
Cell
},
props: {
time: ''
},
data() {
return {
show: false,
nowTime: ''
}
},
computed: {
getYears() {
return this.$store.getters.getYears
},
getMonths() {
return this.$store.getters.getMonths
},
getDays() {
return this.$store.getters.getDays
}
},
methods: {
changeNotView () {
console.log(this.show)
this.show = false
},
handleLeaveTimeChange(event) {
console.log(event)
},
/**
* 
* @param  {Number}  year 
* @return {Boolean}      
*/
isLeapYear (year) {
return year % 100 !== 0 && year % 4 === 0 || year % 400 === 0
},
/**
* 
* @param  {Date} date    
* @return {Number}       
*/
getMaxDay (date) {
let year = parseFloat(date.getFullYear())
let month = parseFloat(date.getMonth() + 1)
if (month === 2) {
return this.isLeapYear(year) ? 29 : 28
}
return [4, 6, 9, 11].indexOf(month) >= 0 ? 30 : 31
},
getDayArr(date) {
let nowDay = date.getDate()
if(nowDay == 1) {
return ['', 1, 2]
}
if(this.getMaxDay(date) == nowDay) {
return [nowDay-1, nowDay, '']
}
return [nowDay-1, nowDay, nowDay+1]
},
getMonthArr(date) {
let nowMonth = date.getMonth() + 1
if(nowMonth == 1) {
return ['', 1, 2]
}
if(nowMonth == 12) {
return [nowMonth-1, nowMonth, '']
}
return [nowMonth-1, nowMonth, nowMonth+1]
},
getYearArr(date) {
let nowYear = date.getFullYear()
return [nowYear-1, nowYear, nowYear+1]
},
handleTapYear (options) {
if (options.type == 'panend') {
let year = new Date(this.nowTime).getFullYear()
if(options.additionalEvent == 'panup') {
year += 1
this.$store.commit('mutationYears', this.getYearArr(new Date(`${year}-01-01`)))
}
if(options.additionalEvent == 'pandown') {
year -= 1
this.$store.commit('mutationYears', this.getYearArr(new Date(`${year}-01-01`)))
}
}
},
handleTapMonth (options) {
if (options.type == 'panend') {
let year = new Date(this.nowTime).getFullYear()
let month = new Date(this.nowTime).getMonth() + 1
if (options.additionalEvent == 'panup') {
if (month == 12) { return }
month += 1
if (month > 12) { month = 12 }
if(month < 10) {
month = '0' + month
}
this.$store.commit('mutationMonths', this.getMonthArr(new Date(`${year}-${month}-01`)))
}
if (options.additionalEvent == 'pandown') {
if (month == 1) { return }
month -= 1
if (month < 1) { month = 1 }
if (month < 10) {
month = '0' + month
}
this.$store.commit('mutationMonths', this.getMonthArr(new Date(`${year}-${month}-01`)))
}
}
},
handleTapDay (options) {
if (options.type == 'panend') {
let year = new Date(this.nowTime).getFullYear()
let month = new Date(this.nowTime).getMonth() + 1
let day = new Date(this.nowTime).getDate()
if (options.additionalEvent == 'panup') {
let maxDay = this.getMaxDay(new Date(this.nowTime))
if (maxDay == day) { return }
day += 1
if (maxDay < day) { day = maxDay }
if (month < 10) {
month = '0' + month
}
if (day < 10) {
day = '0' + day
}
this.$store.commit('mutationDays', this.getDayArr(new Date(`${year}-${month}-${day}`)))
}
if (options.additionalEvent == 'pandown') {
if (day == 1) return;
day -= 1
if (day < 1) { day = 1 }
if (month < 10) {
month = '0' + month
}
if (day < 10) {
day = '0' + day
}
this.$store.commit('mutationDays', this.getDayArr(new Date(`${year}-${month}-${day}`)))
}
}
},
handleNowTime: function() {
this.$emit('update-time', this.nowTime)
this.show = false
}
},
watch: {

'$route': 'changeNotView',
getYears: function() {
let nowMonth = this.getMonths[1]
let nowDay = this.getDays[1]
if(nowMonth < 10) {
nowMonth = '0' + nowMonth
}
if(nowDay < 10) {
nowDay = '0' + nowDay
}
this.nowTime = `${this.getYears[1]}-${nowMonth}-${nowDay}`
let maxDay = this.getMaxDay(new Date(`${this.getYears[1]}-${nowMonth}-01`))
if(maxDay < this.getDays[1]) {
this.nowTime = `${this.getYears[1]}-${nowMonth}-${maxDay}`
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
} else {
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
}
},
getMonths: function() {
let nowMonth = this.getMonths[1]
let nowDay = this.getDays[1]
if (nowMonth < 10) {
nowMonth = '0' + nowMonth
}
if (nowDay < 10) {
nowDay = '0' + nowDay
}
this.nowTime = `${this.getYears[1]}-${nowMonth}-${nowDay}`
let maxDay = this.getMaxDay(new Date(`${this.getYears[1]}-${nowMonth}-01`))
if(maxDay < this.getDays[1]) {
this.nowTime = `${this.getYears[1]}-${nowMonth}-${maxDay}`
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
} else {
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
}
},
getDays: function() {
let nowMonth = this.getMonths[1]
let nowDay = this.getDays[1]
if (nowMonth < 10) {
nowMonth = '0' + nowMonth
}
if (nowDay < 10) {
nowDay = '0' + nowDay
}
this.nowTime = `${this.getYears[1]}-${nowMonth}-${nowDay}`
},
show: function (val) {
if(!val) {
this.nowTime = this.time
this.$store.commit('mutationDays', this.getDayArr(new Date(this.nowTime)))
this.$store.commit('mutationMonths', this.getMonthArr(new Date(this.nowTime)))
this.$store.commit('mutationYears', this.getYearArr(new Date(this.nowTime)))
}
}
},
created() {
this.$store.commit('mutationDays', this.getDayArr(new Date()))
this.$store.commit('mutationMonths', this.getMonthArr(new Date()))
this.$store.commit('mutationYears', this.getYearArr(new Date()))
let unwatch = this.$watch('nowTime', function (newVal, oldVal) {
if(oldVal == '' && newVal != '') {
this.$emit('update-time', newVal)
unwatch()
}
})
}
}



