
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { format } from '@/utils/format'
import MarginBottom from '@/components/attendance/MarginBottom'
import LingPickerTime from '@/components/attendance/LingPickerTime'
export default {
name: 'PrincipalAttendance',
components: {
MarginBottom,
Group,
XHeader,
LingPickerTime
},
data() {
return {
selectTime: format(new Date(), 'YYYY-MM-DD')
}
},
methods: {
/**
* 
* @param time
*/
handleTime: function(time) {
this.selectTime = time
}
}
}



